/*===============================
=            Choices            =
===============================*/

$choices-selector: 'choices' !default;
$choices-font-size-lg: 16px !default;
$choices-font-size-md: 14px !default;
$choices-font-size-sm: 12px !default;
$choices-guttering: 24px !default;
$choices-border-radius: 0 !default;
$choices-border-radius-item: 20px !default;
$choices-bg-color: #fafafa !default;
$choices-bg-color-disabled: #ffffff !default;
$choices-bg-color-dropdown: #ffffff !default;
$choices-text-color: #424242 !default;
$choices-keyline-color: #e0e0e0 !default;
$choices-primary-color: #00bcd4 !default;
$choices-disabled-color: #eaeaea !default;
$choices-highlight-color: $choices-primary-color !default;
$choices-button-dimension: 8px !default;
$choices-button-offset: 8px !default;
$choices-icon-cross: url(data:image/svg+xml;base64,) !default;
$choices-icon-cross-inverse: url(data:image/svg+xml;base64,) !default;
$choices-z-index: 1;

.#{$choices-selector} {
  position: relative;
  overflow: hidden;
  margin-bottom: $choices-guttering;
  @include rem(font-size, $mainFontSizeMobile);
  @media (min-width: 768px){
    @include rem(font-size, $mainFontSize);
  }
  &:focus {
    outline: none;
  }
  &:last-child {
    margin-bottom: 0;
  }
  &.is-open {
    overflow: initial;
  }
  &.is-disabled {
    opacity: .5;
    .#{$choices-selector}__inner,
    .#{$choices-selector}__input {
      background-color: $choices-bg-color-disabled;
      cursor: not-allowed;
      user-select: none;
    }
    .#{$choices-selector}__item {
      cursor: not-allowed;
    }
  }

  [hidden] {
    display: none !important;
  }
}

.#{$choices-selector}[data-type*='select-one'] {
  cursor: pointer;
  .#{$choices-selector}__inner {
    padding-bottom: 7.5px;
  }
  .#{$choices-selector}__input {
    display: block;
    width: 100%;
    padding: 10px;
    border-bottom: 1px solid $choices-keyline-color;
    background-color: #ffffff;
    margin: 0;
  }
  .#{$choices-selector}__button {
    background-image: $choices-icon-cross-inverse;
    padding: 0;
    background-size: 8px;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -10px;
    margin-right: 25px;
    height: 20px;
    width: 20px;
    border-radius: 10em;
    opacity: 0.25;
    &:hover,
    &:focus {
      opacity: 1;
    }
    &:focus {
      box-shadow: 0px 0px 0px 2px $choices-highlight-color;
    }
  }
  .#{$choices-selector}__item[data-value=''] .#{$choices-selector}__button {
    display: none;
  }
  &:after {
    content: '';
    height: 9px;
    width: 12px;
    background: {
      image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M6 9L0.803849 -9.78799e-07L11.1962 -7.02746e-08L6 9Z' fill='%23424242'/%3E%3C/svg%3E");
      size: 12px 9px;
    }
    position: absolute;
    right: 15px;
    top: 50%;
    pointer-events: none;
    transform: translate(0,-50%);
  }
  &.is-open:after {
    transform: translate(0,-50%) rotate(180deg);
  }
  &[dir='rtl'] {
    &:after {
      left: 11.5px;
      right: auto;
    }
    .#{$choices-selector}__button {
      right: auto;
      left: 0;
      margin-left: 25px;
      margin-right: 0;
    }
  }
}

.#{$choices-selector}[data-type*='select-multiple'],
.#{$choices-selector}[data-type*='text'] {
  .#{$choices-selector}__inner {
    cursor: text;
  }
  .#{$choices-selector}__button {
    position: relative;
    display: inline-block;
    margin-top: 0;
    margin-right: -$choices-button-offset*0.5;
    margin-bottom: 0;
    margin-left: $choices-button-offset;
    padding-left: $choices-button-offset * 2;
    border-left: 1px solid darken($choices-primary-color, 10%);
    background-image: $choices-icon-cross;
    background-size: $choices-button-dimension;
    width: $choices-button-dimension;
    line-height: 1;
    opacity: 0.75;
    border-radius: 0;
    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.#{$choices-selector}__inner {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  background-color: $choices-bg-color;
  padding: 7.5px 7.5px 3.75px;
  border: 1px solid $choices-keyline-color;
  border-radius: $choices-border-radius;
  @include rem(font-size, $mainFontSizeMobile);
  min-height: 44px;
  overflow: hidden;
  @media (min-width: 768px){
    @include rem(font-size, $mainFontSize);
  }
  .is-focused &,
  .is-open & {
    //border-color: darken($choices-keyline-color, 15%);
  }
  .is-open & {
    background: #FAFCFF;
    border-bottom: 1px solid transparent;
    border-radius: $choices-border-radius $choices-border-radius 0 0;
  }
  .is-flipped.is-open & {
    background: #FAFCFF;
    border-top: 1px solid transparent;
    border-bottom: 1px solid $choices-keyline-color;
    border-radius: 0 0 $choices-border-radius $choices-border-radius;
  }
}

.#{$choices-selector}__list {
  margin: 0;
  padding-left: 0;
  li {
    list-style: none;
  }
}

.#{$choices-selector}__list--single {
  display: inline-block;
  padding: 4px 26px 4px 4px;
  width: 100%;
  [dir='rtl'] & {
    padding-right: 4px;
    padding-left: 16px;
  }
  .#{$choices-selector}__item {
    width: 100%;
  }
}

.#{$choices-selector}__list--multiple {
  display: inline;
  .#{$choices-selector}__item {
    display: inline-block;
    vertical-align: middle;
    border-radius: $choices-border-radius-item;
    padding: 4px 10px;
    font-size: $choices-font-size-sm;
    font-weight: 500;
    margin-right: 3.75px;
    margin-bottom: 3.75px;
    background-color: $choices-primary-color;
    border: 1px solid darken($choices-primary-color, 5%);
    color: #ffffff;
    word-break: break-all;
    box-sizing: border-box;
    &[data-deletable] {
      padding-right: 5px;
    }
    [dir='rtl'] & {
      margin-right: 0;
      margin-left: 3.75px;
    }
    &.is-highlighted {
      background-color: darken($choices-primary-color, 5%);
      border: 1px solid darken($choices-primary-color, 10%);
    }
    .is-disabled & {
      background-color: darken($choices-disabled-color, 25%);
      border: 1px solid darken($choices-disabled-color, 35%);
    }
  }
}

.#{$choices-selector}__list--dropdown {
  visibility: hidden;
  z-index: $choices-z-index;
  position: absolute;
  width: 100%;
  background-color: $choices-bg-color-dropdown;
  border: {
    top: none;
    left: 1px solid $choices-keyline-color;
    right: 1px solid $choices-keyline-color;
    bottom: 1px solid $choices-keyline-color;
  }
  top: 100%;
  margin-top: -1px;
  border-bottom-left-radius: $choices-border-radius;
  border-bottom-right-radius: $choices-border-radius;
  overflow: hidden;
  word-break: break-all;
  will-change: visibility;
  &.is-active {
    visibility: visible;
  }
  .is-open & {
    //border-color: darken($choices-keyline-color, 15%);
  }
  .is-flipped & {
    top: auto;
    bottom: 100%;
    margin-top: 0;
    margin-bottom: -1px;
    border-radius: 0.25rem 0.25rem 0 0;
    border: {
      top: 1px solid $choices-keyline-color;
      bottom: none;
    }
  }
  .#{$choices-selector}__list {
    position: relative;
    max-height: 300px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    will-change: scroll-position;
  }
  .#{$choices-selector}__item {
    position: relative;
    padding: 11px 16px;
    font-size: $choices-font-size-lg;
    line-height: 1.6;
    [dir='rtl'] & {
      text-align: right;
    }
    &:nth-child(odd){
      background: #FAFCFF;
    }

    .gfield_contains_required & {
      &.choices__placeholder {
        display: none;
      }
    }
  }
  .#{$choices-selector}__item--selectable {
    //@media (min-width: 640px) {
    //  padding-right: 100px;
    //  &:after {
    //    content: attr(data-select-text);
    //    font-size: $choices-font-size-sm;
    //    opacity: 0;
    //    position: absolute;
    //    right: 10px;
    //    top: 50%;
    //    transform: translateY(-50%);
    //  }
    //  [dir='rtl'] & {
    //    text-align: right;
    //    padding-left: 100px;
    //    padding-right: 10px;
    //    &:after {
    //      right: auto;
    //      left: 10px;
    //    }
    //  }
    //}
    &.is-selected {
      font-weight: bold;
    }
  }
}

.#{$choices-selector}__item {
  cursor: default;
}

.#{$choices-selector}__item--selectable {
  cursor: pointer;
}

.#{$choices-selector}__item--disabled {
  cursor: not-allowed;
  user-select: none;
  opacity: 0.5;
}

.#{$choices-selector}__heading {
  font-weight: 600;
  font-size: $choices-font-size-sm;
  padding: 10px;
  border-bottom: 1px solid lighten($choices-keyline-color, 10%);
  color: lighten(#333, 30%);
}

.#{$choices-selector}__button {
  text-indent: -9999px;
  -webkit-appearance: none;
  appearance: none;
  border: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  &:focus {
    outline: none;
  }
}

.#{$choices-selector}__input {
  display: inline-block;
  vertical-align: baseline;
  background-color: $choices-bg-color;
  font-size: $choices-font-size-md;
  margin-bottom: 5px;
  border: 0;
  border-radius: 0;
  max-width: 100%;
  padding: 4px 0 4px 2px;
  &:focus {
    outline: 0;
  }
  [dir='rtl'] & {
    padding-right: 2px;
    padding-left: 0;
  }
}

.#{$choices-selector}__placeholder {
  font-style: italic;
  color: #424242;
}

/*=====  End of Choices  ======*/
